.wrapper {
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.photo-wrapper {
  height: 90vh;
  width: 75vw;
  overflow: hidden;
  text-align: center;
}

.photo {
  width: auto;
  height: 100%;
  -webkit-transition: opacity 0.75s ease-in;
  -o-transition: opacity 0.75s ease-in;
  transition: opacity 0.75s ease-in;
}



